import React, { useState } from 'react';
import { StyleSheet, Text, View, TouchableOpacity, FlatList, ListRenderItem } from 'react-native';

interface Trip {
  id: number;
  start: string;
  end: string;
  status: string;
}

export default function DriverStatusScreen() {
  const [availability, setAvailability] = useState<boolean>(true); // Default to "Available"
  const [assignedTrips, setAssignedTrips] = useState<Trip[]>([
    { id: 1, start: 'Bridgetown', end: 'Holetown', status: 'Pending' },
    { id: 2, start: 'Oistins', end: 'Speightstown', status: 'Pending' },
  ]);

  const toggleAvailability = () => {
    setAvailability(!availability);
  };

  const handleTripAction = (tripId: number, newStatus: string) => {
    setAssignedTrips((prevTrips) =>
      prevTrips.map((trip) =>
        trip.id === tripId ? { ...trip, status: newStatus } : trip
      )
    );
  };

  const renderTrip: ListRenderItem<Trip> = ({ item }) => (
    <View style={styles.tripCard}>
      <Text style={styles.tripDetails}>
        From: {item.start} {"\n"}To: {item.end} {"\n"}Status: {item.status}
      </Text>
      {item.status === 'Pending' && (
        <View style={styles.actionButtons}>
          <TouchableOpacity
            style={[styles.actionButton, styles.inProgressButton]}
            onPress={() => handleTripAction(item.id, 'In Progress')}
          >
            <Text style={styles.actionText}>Start</Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={[styles.actionButton, styles.cancelButton]}
            onPress={() => handleTripAction(item.id, 'Cancelled')}
          >
            <Text style={styles.actionText}>Cancel</Text>
          </TouchableOpacity>
        </View>
      )}
      {item.status === 'In Progress' && (
        <TouchableOpacity
          style={[styles.actionButton, styles.completeButton]}
          onPress={() => handleTripAction(item.id, 'Completed')}
        >
          <Text style={styles.actionText}>Complete</Text>
        </TouchableOpacity>
      )}
    </View>
  );

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Driver Status</Text>
      <Text style={styles.availability}>
        Current Status: {availability ? 'Available' : 'Not Available'}
      </Text>
      <TouchableOpacity style={styles.toggleButton} onPress={toggleAvailability}>
        <Text style={styles.toggleButtonText}>
          {availability ? 'Go Offline' : 'Go Online'}
        </Text>
      </TouchableOpacity>
      <Text style={styles.sectionTitle}>Assigned Trips</Text>
      <FlatList
        data={assignedTrips}
        renderItem={renderTrip}
        keyExtractor={(item) => item.id.toString()}
        contentContainerStyle={styles.tripList}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 20,
    backgroundColor: '#F5FCFF',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 10,
  },
  availability: {
    fontSize: 18,
    marginBottom: 15,
  },
  toggleButton: {
    backgroundColor: '#007BFF',
    padding: 10,
    borderRadius: 8,
    alignItems: 'center',
    marginBottom: 20,
  },
  toggleButtonText: {
    color: '#FFFFFF',
    fontSize: 16,
  },
  sectionTitle: {
    fontSize: 20,
    fontWeight: 'bold',
    marginBottom: 10,
  },
  tripList: {
    paddingBottom: 20,
  },
  tripCard: {
    backgroundColor: '#FFFFFF',
    borderWidth: 1,
    borderColor: '#ddd',
    borderRadius: 8,
    padding: 15,
    marginBottom: 15,
    shadowColor: '#000',
    shadowOpacity: 0.1,
    shadowOffset: { width: 0, height: 1 },
    shadowRadius: 1,
    elevation: 2,
  },
  tripDetails: {
    fontSize: 16,
    marginBottom: 10,
  },
  actionButtons: {
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  actionButton: {
    padding: 10,
    borderRadius: 8,
    alignItems: 'center',
    marginHorizontal: 5,
  },
  inProgressButton: {
    backgroundColor: '#FFC107',
  },
  completeButton: {
    backgroundColor: '#28A745',
  },
  cancelButton: {
    backgroundColor: '#DC3545',
  },
  actionText: {
    color: '#FFFFFF',
    fontSize: 14,
  },
});
