import React from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import AuthScreen from './AuthScreen';
import TripRequestForm from './TripRequestForm';
import DriverStatusScreen from './DriverStatusScreen';

const Stack = createStackNavigator();

export default function App() {
  return (
    <NavigationContainer>
      <Stack.Navigator initialRouteName="LoginSignup">
        <Stack.Screen name="LoginSignup" component={AuthScreen} />
        <Stack.Screen name="TripRequestForm" component={TripRequestForm} />
        <Stack.Screen name="DriverStatus" component={DriverStatusScreen} />
      </Stack.Navigator>
    </NavigationContainer>
  );
}
